import React, { useEffect, useState } from 'react';
import { Button, Col, Container, Row, Table } from 'reactstrap';
import { Rep } from '../Repetition/Rep';

export const Serie = ({ title, data, min, max }) => {
  const [_data, setData] = useState(data);
  const [_max, setMin] = useState(max);
  const [_min, setMax] = useState(min);

  useEffect(() => {
    setData(
      _data.map(item => {
        return { ...item, editable: true };
      }),
    );
  }, []);

  const handleOnSave = () => {
    console.log('saving');
  };

  const handleOnAdd = () => {
    if (_data.length < _max) {
      console.log('entro');
      setData([
        ..._data,
        {
          id: _data.length + 1,
          num: { value: 0, type: _data[0].num.type },
          weigth: { value: 0, type: data[0].weigth.type },
          editable: true,
        },
      ]);
    } else console.log('That is the max!!!');
  };

  const handleOnDelete = () => {
    if (_data.length > _min) {
      setData(_data.filter(item => item.id !== _data.length));
    } else console.log('That is the min!!!');
  };

  const onItemChange = item => {
    setData(
      _data.map(serie => {
        if (serie.id === item.id) {
          return item;
        }
        return serie;
      }),
    );
  };

  return (
    <Container>
      <Row>
        <h2>{title}</h2>
      </Row>
      <Row>
        <Table>
          <thead>
            <tr>
              {Object.keys(data[0]).map(c => (
                <th key={c}>{c}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {_data.map(item => (
              <Rep key={item.id} item={item} onItemChange={onItemChange}></Rep>
            ))}
          </tbody>
        </Table>
      </Row>
      <Row>
        <Col xs={8}>
          <Button color="success" onClick={handleOnSave}>
            Save
          </Button>
        </Col>
        <Col xs={4}>
          <Button color="danger" onClick={handleOnDelete}>
            -
          </Button>
          <Button color="primary" onClick={handleOnAdd}>
            +
          </Button>
        </Col>
      </Row>
    </Container>
  );
};
